"use client";

import { useState } from "react";
import { Check, Copy, Terminal } from "lucide-react";
import { BorderBeam } from "@/components/ui/border-beam";
import { cn } from "@/lib/utils";

interface CodeTerminalProps {
  code: string;
  language?: string;
  label?: string;
  className?: string;
}

export function CodeTerminal({ code, language = "json", label = "config.json", className }: CodeTerminalProps) {
  const [copied, setCopied] = useState(false);

  const handleCopy = async () => {
    await navigator.clipboard.writeText(code);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const lines = code.split("\n");

  return (
    <div className={cn("relative rounded-xl overflow-hidden border border-border/40 bg-[#0c0c0c] shadow-2xl", className)}>
      <BorderBeam size={300} duration={12} delay={9} colorFrom="#86a447" colorTo="#31593a" />
      
      {/* Header */}
      <div className="flex items-center justify-between px-4 py-3 bg-[#1a1a1a] border-b border-white/5 relative z-10">
        <div className="flex items-center gap-4">
          <div className="flex space-x-1.5">
            <div className="w-3 h-3 rounded-full bg-[#ff5f56]" />
            <div className="w-3 h-3 rounded-full bg-[#ffbd2e]" />
            <div className="w-3 h-3 rounded-full bg-[#27c93f]" />
          </div>
          <div className="flex items-center gap-2 px-3 py-1 rounded-md bg-[#252526] border border-white/5">
            <Terminal className="w-3 h-3 text-muted-foreground" />
            <span className="text-xs text-muted-foreground font-mono">{label}</span>
          </div>
        </div>
        <button
          onClick={handleCopy}
          className="p-1.5 rounded-md hover:bg-white/10 transition-colors text-muted-foreground hover:text-foreground"
          aria-label="Copy code"
        >
          {copied ? <Check className="w-4 h-4 text-green-500" /> : <Copy className="w-4 h-4" />}
        </button>
      </div>

      {/* Code Body */}
      <div className="p-6 overflow-x-auto relative z-10">
        <pre className="text-sm font-mono leading-relaxed flex gap-4">
          {/* Line Numbers */}
          <div className="flex flex-col text-right select-none text-muted-foreground/30 text-xs py-[2px]">
            {lines.map((_, i) => (
              <span key={i} className="leading-relaxed h-6">{i + 1}</span>
            ))}
          </div>
          
          {/* Code Content */}
          <code className={cn("language-" + language, "text-gray-300 flex-1")}>
            {lines.map((line, i) => (
              <div key={i} className="h-6 leading-relaxed whitespace-pre">
                {/* Simple syntax highlighting simulation for keys vs values (mostly for JSON/YAML) */}
                {line.split(/(: )/).map((part, j) => {
                  if (j === 0 && line.includes(":")) {
                    // Key (e.g., "agent_role")
                    return <span key={j} className="text-blue-400">{part}</span>;
                  } else if (part === ": ") {
                    // Separator
                    return <span key={j} className="text-gray-500">{part}</span>;
                  } else {
                    // Value
                    return <span key={j} className={part.match(/^".*"$/) ? "text-green-400" : "text-orange-400"}>{part}</span>;
                  }
                })}
              </div>
            ))}
          </code>
        </pre>
      </div>
    </div>
  );
}
